import React, { FC, useMemo } from 'react';
import { DatePicker, Button, Row, Col } from 'antd';
import moment, { Moment } from 'moment';
import styles from './index.less';
import { TimeItem } from './types';

interface Props {
  value?: string;
  onChange?: (time?: string) => void;
  mode?: 'vertical' | 'horizontal';
  timeList?: TimeItem[];
  showTimeFormat?: string;
}
const initTimeList = [
  { lable: '3h', time: moment().add(3, 'h') },
  { lable: '12h', time: moment().add(12, 'h') },
  { lable: '48h', time: moment().add(48, 'h') },
  { lable: '1', time: moment().add(7, 'd') },
];


const ExpTime: FC<Props> = (props: any) => {
  const {
    value: expireTime,
    // eslint-disable-next-line no-empty-function
    onChange = () => {},
    mode = 'vertical',
    timeList = initTimeList,
    showTimeFormat = 'HH:mm',
    ...DatePickerProps
  } = props;
  
  function handleChange(time: Moment | null) {
    onChange(time ? time.format('YYYY-MM-DD HH:mm:ss') : '');
  }

  const isVertical = useMemo(() => mode === 'vertical', [mode]);

  return (
    <Row>
      <Col span={ isVertical ? 24 : 12 }>
        <DatePicker style={{ width: 200 }}
          allowClear={ false }
          placeholder=""
          showTime={{ format: showTimeFormat }}
          format="YYYY-MM-DD HH:mm:ss"
          value={ expireTime ? moment(expireTime) : undefined }
          onChange={ handleChange }
          { ...DatePickerProps }
        />
      </Col>
      <Col span={ isVertical ? 24 : 12 }>
        {timeList.map((item: TimeItem, index: number) => (
          <Button key={ index }
            className={ styles.timeItem }
            onClick={ () => {
              handleChange(item.time);
            } }
          >
            {item.lable}
          </Button>
        ))}
      </Col>
    </Row>
  );
};

export default ExpTime;
