const type = 'DragableBodyRow';

interface Props {
  username: string;
}

interface DragableBodyRowType {
  index: number;
  moveRow: any;
  className: any;
  style: any;
  [key: string]: any;
}
interface KeyValue {
  [key: string]: any;
}

const DragableBodyRow = ({
  index,
  moveRow,
  className,
  style,
  ...restProps
}: DragableBodyRowType) => {
  const ref = useRef<any>();
  const [{ isOver, dropClassName }, drop] = useDrop({
    accept: type,
    collect: (monitor: any) => {
      const { index: dragIndex } = monitor.getItem() || {};

      if (dragIndex === index) {
        return {};
      }

      return {
        isOver: monitor.isOver(),
        dropClassName:
          dragIndex < index ? ' drop-over-downward' : ' drop-over-upward',
      };
    },
    drop: (item: any) => {
      moveRow(item.index, index);
    },
  });
  const [, drag] = useDrag({
    type,
    item: { index },
    collect: (monitor: any) => ({
      isDragging: monitor.isDragging(),
    }),
  });

  drop(drag(ref));

  return (
    <tr ref={ ref }
      className={ `${ className }${ isOver ? dropClassName : '' }` }
      style={{ cursor: 'move', ...style }}
      { ...restProps }
    />
  );
};

const HotRecomment: React.FC<Props> = (props: Props) => {
  const { username } = props;
  const [loading, setLoading] = useState(false);
  const [tableData, setTableData] = useState<AdjustTableData[]>([]); // 
  const [baseTableData, setBaseTableData] = useState<AdjustTableData[]>([]); // 
  const [replacedData, setReplacedData] = useState<any>({}); // 
  const [rangTimeModalVisible, setRangTimeModalVisible] = useState(false);
  const [modifyRangTimeIndex, setModifyRangTimeIndex] = useState(0);
  const [submitLoading, setSubmitLoading] = useState(false);
  const [rangTimeform] = Form.useForm();
  const [haveModified, setHaveModified] = useState(false);
  const leaveLockRef = useRef(false);
  const history = useHistory();
  const editRef = useRef();

  const initData = async () => {
    setLoading(true);
    // 
    const res = await getHeatRecommendationList();

    if (res.code === 200) {
      let result = res?.data || [];

      result = result.sort((a, b) => a.position - b.position);
      const nowTimeValue = moment().valueOf();
      const existOverdue = result.some(
        (r: AdjustRecommentListItem) => nowTimeValue >= r.endTime
      ); // 

      if (existOverdue) {
        Modal.info({
          title: '',
        });
      }
      if (result.length > 0) {
        if (result.length < 12) {
          result = [...result, ...new Array(12 - result.length).fill({})]
        }
        setTableData(result);
        setBaseTableData(result);
      } else {
        const newArray = new Array(12).fill({});

        setTableData(newArray);
        setBaseTableData(newArray);
      }

      setLoading(false);
    } else {
      setLoading(false);
    }
  };

  useEffect(() => {
    initData();
  }, []);

  // 
  const replaceHandle = (record: AdjustTableData, index: number) => {
    editRef.current.setModalVisible(true);
    setReplacedData({ ...record, index });
  };

  const insertTable = (data) => {
    const { index } = replacedData;

    data.position = index + 1;
    const temp = [...tableData];

    temp.splice(index, 1, data);
    setTableData(temp);
    console.log(temp);
  };

  // 
  const changeRangTime = (index: number) => {
    setRangTimeModalVisible(true);
    setModifyRangTimeIndex(index);
  };

  const columns = [
    {
      title: (
        <React.Fragment>
          
          <br />
          ()
        </React.Fragment>
      ),
      width: 130,
      key: 'paixu',
      render: (text: string, record: any, index: number) => index + 1,
    },
    {
      title: '',
      dataIndex: 'title',
      key: 'title',
      render: (text: string, record: any) => (
        <a href={ `/platform/windvane/content-manage/filmList-manage/add?dramaId=${ record.listId }` } target="_blank">
          {text}
        </a>
      ),
    },
    {
      title: '',
      dataIndex: 'type',
      key: 'type',
      render: text => {
        return <div>{FilmTypeOptions[text]}</div>
      },
    },
    // {
    //   title: (
    //     <React.Fragment>
    //       
    //       <br />
    //       ()
    //     </React.Fragment>
    //   ),
    //   width: 130,
    //   dataIndex: 'coverImage',
    //   key: 'coverImage',
    //   render: (text: string) => {
    //     if (text) {
    //       const coverImageList = JSON.parse(text);

    //       if (coverImageList.length > 0) {
    //         return <PreviewImg imgUrls={ coverImageList[0] } placement="right" />;
    //       }
    //     }

    //     return '';
    //   },
    // },
    {
      title: '',
      dataIndex: 'followTotal',
      key: 'followTotal',
    },
    {
      title: '',
      dataIndex: 'updateTimeLong',
      key: 'updateTimeLong',
      render: (text) => {
        return text ? (
          <span>{moment(text).format('YYYY-MM-DD HH:mm:ss')}</span>
        ) : null;
      },
    },
    {
      title: '',
      dataIndex: 'operator',
      key: 'operator',
    },
    {
      title: (
        <React.Fragment>
          
          <br />
          ()
        </React.Fragment>
      ),
      dataIndex: 'endTime',
      key: 'endTime',
      render: (text: string, record: any, index: number) => {
        const now = new Date().getTime();
        const dom
          = text && parseInt(text) > now ? (
            <div onClick={ () => changeRangTime(index) }>
              <Countdown time={ text } overTimeText="" />
            </div>
          ) : (
            ''
          );

        return dom;
      },
    },
    {
      title: '',
      dataIndex: '',
      key: 'operate',
      width: 80,
      render: (text: string, record: any, index: number) => {
        return (
          <Space>
            <Button type="primary"
              size="small"
              onClick={ () => replaceHandle(record, index) }
              shape="round"
            >
              
            </Button>
          </Space>
        );
      },
    },
  ];
  const components = {
    body: {
      row: DragableBodyRow,
    },
  };

  // 
  const moveRow = useCallback(
    /**
     *
     * @param dragIndex 
     * @param hoverIndex 
     */
    (dragIndex: number, hoverIndex: number) => {
      const dragRow = tableData[dragIndex];

      setTableData(
        update(tableData, {
          $splice: [
            [dragIndex, 1],
            [hoverIndex, 0, dragRow],
          ],
        })
      );
      setHaveModified(true);
      leaveLockRef.current = true;
    },
    [tableData]
  );

  // 
  const modifyConfirmhandle = () => {
    setSubmitLoading(true);
    // 12

    if (tableData.filter((item) => item.title).length < 12) {
      Modal.warn({
        title: '12',
      });
      setSubmitLoading(false);

      return;
    }


    console.log(tableData);

    const nowTimeValue = moment().valueOf();
    const existOverdue = tableData.some(
      (r: any) => nowTimeValue >= r.endTime
    ); // 

    if (existOverdue) {
      Modal.warn({
        title: '',
      });
      setSubmitLoading(false);

      return;
    }
    const params = tableData.map((item, index) => {
      return { ...item, position: index + 1 };
    });

    // todo    
    upsertHotList(params)
      .then((res: ResponseData<any>) => {
        if (res.code === 200) {
          setSubmitLoading(false);
          Modal.success({
            title: '',
            onOk: () => {
              initData();
              setHaveModified(false);
              leaveLockRef.current = false;
            },
          });
        } else {
          setSubmitLoading(false);
        }
      })
      .catch(() => {
        setSubmitLoading(false);
      });
  };

  const rangTimeModalOkHandle = () => {
    rangTimeform.validateFields().then((values: KeyValue) => {
      const copyTableData = [...tableData];

      copyTableData[modifyRangTimeIndex]['endTime'] = moment(values.onRankTime).valueOf();

      setTableData(copyTableData);
      message.success('');
      rangTimeform.resetFields();
      setRangTimeModalVisible(false);
      setHaveModified(true);
      leaveLockRef.current = true;
    });
  };

  const rangTimeModalCancelHandle = () => {
    rangTimeform.resetFields();
    setRangTimeModalVisible(false);
  };

  return (
    <React.Fragment>
      <Prompt when={ haveModified }
        message={ (location: any) => {
          if (!leaveLockRef.current) {
            return true;
          }
          Modal.confirm({
            title: '',
            content: ', ',
            onOk: () => {
              leaveLockRef.current = false;
              setHaveModified(false);
              history.push(location.pathname);
            },
          });

          return !leaveLockRef.current;
        } }
      />

      <div style={{ paddingBottom: '72px' }}>
        <DndProvider backend={ HTML5Backend }>
          <Table columns={ columns }
            loading={ loading }
            dataSource={ tableData }
            components={ components }
            rowKey="list_id"
            pagination={ false }
            bordered={ true }
            onRow={ (record: any, index: number) => ({
              index,
              moveRow,
            }) }
          />
        </DndProvider>
      </div>

      <Row justify="center"
        style={{
          backgroundColor: '#fff',
          position: 'fixed',
          bottom: '0',
          right: '24px',
          width: 'calc(100% - 256px)',
          padding: '20px 0',
          borderTop: '1px solid #ccc',
        }}
      >
        <Col>
          <Popconfirm
            title=""
            onConfirm={() => {
              setTableData(baseTableData);
              setHaveModified(false);
              leaveLockRef.current = false;
            }}
          >
            <Button></Button>
          </Popconfirm>
        </Col>
        <Col offset={ 2 }>
          <Popconfirm title=""
            onConfirm={ () => {
              modifyConfirmhandle();
            } }
          >
            <Button type="primary" loading={ submitLoading }>
              
            </Button>
          </Popconfirm>
        </Col>
      </Row>

      <EditForm ref={ editRef } insertTable={ insertTable } tableData={ tableData } />
      <Modal title=""
        visible={ rangTimeModalVisible }
        onOk={ rangTimeModalOkHandle }
        onCancel={ rangTimeModalCancelHandle }
      >
        <Form form={ rangTimeform } labelCol={{ span: 4 }}>
          <Form.Item label=""
            name="onRankTime"
            rules={ [{ required: true, message: '' }] }
          >
            <ExpTime showTimeFormat="HH:mm:ss" />
          </Form.Item>
        </Form>
      </Modal>
    </React.Fragment>
  );
};

export default withAuthorized(HotRecomment, [Role.SYSTEM]);
