import React, {
  useState,
  useCallback,
  useRef,
  forwardRef,
  useImperativeHandle,
} from 'react';
import ErrorBoundary from '@/components/ErrorBoundary/index';
import {
  Table,
  Space,
  Button,
  Modal,
  Form,
  Row,
  Col,
  message,
  Popconfirm,
  Input,
  Radio,
} from 'antd';
import moment from 'moment';
import DramaContentSearch from '@/components/FilmContentSearch';
import ExpTime from '@/components/ExpTime';

interface KeyValue {
  [key: string]: any;
}

const Edit = (props, ref) => {
  const { tableData, insertTable } = props;
  const [modalVisible, setModalVisible] = useState(false);
  const [form] = Form.useForm();
  const [type, setType] = useState('1');
  //  
  const modalOkHandle = () => {
    form.validateFields().then((values: KeyValue) => {
      const { dramaInfo, onRankTime } = values;

      console.log(values, tableData);
      const { label, value, totalItem } = dramaInfo;
      const { followTotal = 0 } = JSON.parse(totalItem);

      // 
      if (tableData.find((r: any) => r.listId === value)) {
        message.warn('');

        return;
      }
      // 
      const formatData: any = {
        title: label,
        endTime: moment(onRankTime).valueOf(),
        listId: value,
        type,
        followTotal,
      };

      insertTable(formatData);
      setModalVisible(false);
      form.resetFields();

    });
  };
  //  
  const modalCancelHandle = () => {
    setModalVisible(false);
    form.resetFields();
  };
  const onFormLayoutChange = (params) => {
    const { type } = params;

    console.log(params);
    if (type) {
      setType(type);
    }
  };

  useImperativeHandle(ref, () => ({
    setModalVisible: (showStatus: boolean) => {
      setModalVisible(showStatus);
    },
  }));

  return (
    <Modal title=""
      visible={ modalVisible }
      onOk={ modalOkHandle }
      onCancel={ modalCancelHandle }
    >
      <Form form={ form }
        labelCol={{ span: 4 }}
        initialValues={{ type }}
        onValuesChange={ onFormLayoutChange }
      >
        <Form.Item label="" name="type">
          <Radio.Group>
            <Radio.Button value="1" key="1"></Radio.Button>
            <Radio.Button value="2" key="2"></Radio.Button>
          </Radio.Group>
        </Form.Item>
        <Form.Item label=""
          name="dramaInfo"
          rules={ [{ required: true }] }
        >
          <DramaContentSearch type={ type } />
        </Form.Item>
        <Form.Item label=""
          name="onRankTime"
          rules={ [{ required: true }] }
        >
          <ExpTime showTimeFormat="HH:mm:ss" />
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default forwardRef(Edit);
