import React, { FC } from 'react';
import { Tabs } from 'antd';
import { withAuthorized } from '@/utils/Authorized';
import { Role } from '@/utils/authority';
import HotSearch from './components/HotSearch';
import HotRecomment from './components/HotRecomment';
import FilmRecommend from './components/FilmRecommend';
import { useUserRole } from '@/services/login';


const { TabPane } = Tabs;

const HotManage: FC = () => {
  const { data: userInfo } = useUserRole();
  const username = userInfo?.data?.username || '';

  return (
    <Tabs type="card" defaultActiveKey="piandan-recommend">
      <TabPane tab="" key="hot-search">
        <HotSearch username={ username } />
      </TabPane>
      <TabPane tab="" key="hot-recommend">
        <HotRecomment username={ username } />
      </TabPane>
      <TabPane tab="" key="piandan-recommend">
        <FilmRecommend username={ username } />
      </TabPane>
    </Tabs>
  );
};

export default withAuthorized(HotManage, [Role.SYSTEM]);
