import React, { FC, useMemo } from 'react';
import { Statistic } from 'antd';
import moment from 'moment';
import { css } from '@emotion/core';

const { Countdown: TimeCountdown } = Statistic;

export interface Props {
  time: number | string;
  overTimeText?: string;
  style?: React.CSSProperties;
  fontSize?: number | string;
}

export const formatExpireTime = (date: any) => {
  const milliSeconds = moment(date).isAfter(moment()) ? moment.duration(moment(date).diff(moment())) : moment.duration(moment().diff(moment(date)));
  let hours = 0;

  hours = parseInt(milliSeconds.asHours() + '');

  return {
    hours,
    days: `${ parseInt(milliSeconds.asDays() + '') }`
  };
};

const Countdown: FC<Props> = ({ time, overTimeText = '', fontSize = '14px', style = { fontSize: '14px', color: '#1890ff' } }: Props) => {
  const { hours, days } = useMemo(() => {
    return formatExpireTime(time);
  }, [time]);

  if (new Date().getTime() >= (typeof time === 'number' ? time : moment(time).valueOf())) return <span style={{ color: '#f81d22' }}>{overTimeText}</span>;

  return (
    hours >= 24
      ? <span style={ style }>{days}</span>
      : (
        <TimeCountdown value={ time } valueStyle={ style } />
      )
  );
};

export default Countdown;
