import React, { useState, useCallback, useRef, useEffect } from 'react';
import { Link, useHistory } from 'umi';
import { Table, Space, Button, Modal, Form, Row, Col, message, Popconfirm } from 'antd';
import { Prompt } from 'react-router-dom';
import { DndProvider, useDrag, useDrop } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import update from 'immutability-helper';
import { withAuthorized } from '@/utils/Authorized';
import { Role } from '@/utils/authority';
import {
  getHeatRecommendationList,
  batchGetDramaContentById,
  getDramaContentById,
  heatRecommendationAdjust
} from '../services';
import DramaContentSearch from '@/components/DramaContentSearch';
import ExpTime from '@/components/ExpTime';
import Countdown from '@/components/Countdown';
import PreviewImg from '@/components/PreviewImg';
import moment from 'moment';
import { AdjustRecommentListItem, AdjustTableData } from '../types';
import { ResponseData, ProductionDetailInfo } from '@/types/types';

const type = 'DragableBodyRow';

interface Props {
  username: string;
}

interface DragableBodyRowType {
  index: number;
  moveRow: any;
  className: any;
  style: any;
  [key: string]: any;
}
interface KeyValue {
  [key: string]: any;
}

const DragableBodyRow = ({ index, moveRow, className, style, ...restProps }: DragableBodyRowType) => {
  const ref = useRef<any>();
  const [{ isOver, dropClassName }, drop] = useDrop({
    accept: type,
    collect: (monitor: any) => {
      const { index: dragIndex } = monitor.getItem() || {};

      if (dragIndex === index) {
        return {};
      }

      return {
        isOver: monitor.isOver(),
        dropClassName: dragIndex < index ? ' drop-over-downward' : ' drop-over-upward',
      };
    },
    drop: (item: any) => {
      moveRow(item.index, index);
    },
  });
  const [, drag] = useDrag({
    type,
    item: { index },
    collect: (monitor: any) => ({
      isDragging: monitor.isDragging(),
    }),
  });

  drop(drag(ref));

  return (
    <tr ref={ ref }
      className={ `${ className }${ isOver ? dropClassName : '' }` }
      style={{ cursor: 'move', ...style }}
      { ...restProps }
    />
  );
};

const HotRecomment: React.FC<Props> = (props: Props) => {
  const { username } = props;
  const [loading, setLoading] = useState(false);
  const [tableData, setTableData] = useState<AdjustTableData[]>([]); // 
  const [baseTableData, setBaseTableData] = useState<AdjustTableData[]>([]); // 
  const [replacedData, setReplacedData] = useState<any>({}); // 
  const [modalVisible, setModalVisible] = useState(false);
  const [rangTimeModalVisible, setRangTimeModalVisible] = useState(false);
  const [modifyRangTimeIndex, setModifyRangTimeIndex] = useState(0);
  const [submitLoading, setSubmitLoading] = useState(false);
  const [form] = Form.useForm();
  const [rangTimeform] = Form.useForm();
  const [haveModified, setHaveModified] = useState(false);
  const leaveLockRef = useRef(false);
  const history = useHistory();

  const initData = () => {
    setLoading(true);
    // 
    getHeatRecommendationList()
      .then((res: ResponseData<any>) => {
        if (res.code === 200) {
          const { result = [], operator } = res?.data;
          const nowTimeValue = moment().valueOf();
          const existOverdue = result.some((r: AdjustRecommentListItem) => nowTimeValue >= r.onRankTime); // 
          const dramaIds = result.map((item: AdjustRecommentListItem) => item.dramaId);

          if (dramaIds.length > 0) {
            batchGetDramaContentById(dramaIds)
              .then((batchRes: ResponseData<any>) => {
                if (batchRes.code === 200) {
                  const { data: adjustDramsDetails } = batchRes;
                  // 
                  const mergeAdjustList = result.map((item: AdjustRecommentListItem) => {
                    const { dramaId, rank } = item; // adjustRank1
                
                    return ({ ...item, ...(adjustDramsDetails[dramaId]), operator, rank: rank - 1 });
                  });
                  // 
                  const existOutlineDrama = mergeAdjustList.some((r: any) => r.replaced && r.onlineStatus === 0);
                  const modalText = existOutlineDrama && existOverdue
                    ? ''
                    : existOutlineDrama
                      ? ''
                      : '';

                  if (existOutlineDrama || existOverdue) {
                    Modal.info({
                      title: `${ modalText }`
                    });
                  }
              
                  setTableData(mergeAdjustList);
                  setBaseTableData(mergeAdjustList);
                  setLoading(false);
                } else {
                  setLoading(false);
                }
              })
              .catch(() => {
                setLoading(false);
              });
          } else {
            setLoading(false);
          }
        } else {
          setLoading(false);
        }
      })
      .catch(() => {
        setLoading(false);
      });
  };

  useEffect(() => {
    initData();
  }, []);
  

  // 
  const replaceHandle = (record: AdjustTableData, index: number) => {
    setModalVisible(true);
    setReplacedData({ ...record, index });
  };

  // 
  const changeRangTime = (index: number) => {
    setRangTimeModalVisible(true);
    setModifyRangTimeIndex(index);
  };

  const columns = [
    {
      title: (
        <React.Fragment>
          <br />()
        </React.Fragment>
      ),
      width: 130,
      render: (text: string, record: any, index: number) => index + 1
    },
    {
      title: '',
      dataIndex: 'title',
      key: 'title',
      render: (text: string, record: any) => <Link to={ `/content-manage/production-manage/${ record.dramaId }` }>{text}</Link>
    },
    {
      title: (
        <React.Fragment>
          <br />()
        </React.Fragment>
      ),
      width: 130,
      dataIndex: 'coverImage',
      key: 'coverImage',
      render: (text: string) => {
        if (text) {
          const coverImageList = JSON.parse(text);

          if (coverImageList.length > 0) {
            return <PreviewImg imgUrls={ coverImageList[0] } placement="right" />;
          }
        }

        return '';
      }
    },
    {
      title: '',
      dataIndex: 'searchHeat',
      key: 'searchHeat',
    },
    {
      title: '',
      dataIndex: 'operator',
      key: 'operator',
    },
    {
      title: '',
      dataIndex: 'onlineStatus',
      key: 'onlineStatus',
      render: (text: number) => {
        return (text === 0 ? <span style={{ color: 'rgb(248, 29, 34)' }}></span> : '');
      }
    },
    {
      title: (
        <React.Fragment>
          <br />()
        </React.Fragment>
      ),
      dataIndex: 'onRankTime',
      key: 'onRankTime',
      render: (text: string, record: any, index: number) => text && (
        <div onClick={ () => changeRangTime(index) }>
          <Countdown time={ text } overTimeText="" />
        </div>
      )
    },
    {
      title: '',
      dataIndex: '',
      key: 'operate',
      width: 80,
      render: (text: string, record: any, index: number) => {
        return (
          <Space>
            <Button type="primary" size="small" onClick={ () => replaceHandle(record, index) }></Button>
          </Space>
        );
      }
    },
  ];

  const components = {
    body: {
      row: DragableBodyRow,
    },
  };

  // 
  const moveRow = useCallback(
    /**
     * 
     * @param dragIndex 
     * @param hoverIndex 
     */
    (dragIndex: number, hoverIndex: number) => {
      const dragRow = tableData[dragIndex];

      setTableData(
        update(tableData, {
          $splice: [
            [dragIndex, 1],
            [hoverIndex, 0, dragRow],
          ],
        }),
      );
      setHaveModified(true);
      leaveLockRef.current = true;
    },
    [tableData],
  );

  //  
  const modalOkHandle = () => {
    form.validateFields().then((values: KeyValue) => {
      const { dramaInfo, onRankTime } = values;
      const { label, value } = dramaInfo;
      const { index } = replacedData;

      // 
      const formatData: any = {
        title: label,
        dramaId: value,
        onRankTime,
        operator: username
      };

      // 
      if (tableData.find((r: any) => r.dramaId === value)) {
        message.warn('');

        return;
      }
      getDramaContentById(value).then((res: ResponseData<ProductionDetailInfo>) => {
        if (res.code === 200) {
          const { coverImage, searchHeat } = res?.data || {};

          formatData.coverImage = coverImage;
          formatData.searchHeat = searchHeat;
          const copyTableData = [...tableData];

          copyTableData[index] = formatData;
          setTableData(copyTableData);
          setModalVisible(false);
          form.resetFields();
          setHaveModified(true);
          leaveLockRef.current = true;
        }
      });
    });
  };
  //  
  const modalCancelHandle = () => {
    setModalVisible(false);
    form.resetFields();
  };
  
  // 
  const modifyConfirmhandle = () => {
    setSubmitLoading(true);
    const sendParams = tableData.map((item: any, index: number) => {
      const { dramaId, onRankTime } = item;

      return ({ dramaId, onRankTime: moment(onRankTime).valueOf(), rank: index + 1 });
    });

    if (tableData.some((r: any) => r.onlineStatus === 0)) {
      setSubmitLoading(false);
      Modal.warn({
        title: ''
      });

      return;
    }

    const nowTimeValue = moment().valueOf();
    const existOverdue = sendParams.some((r: AdjustRecommentListItem) => nowTimeValue >= r.onRankTime); // 

    if (existOverdue) {
      Modal.warn({
        title: ''
      });
      setSubmitLoading(false);

      return;
    }
    
    heatRecommendationAdjust(sendParams)
      .then((res: ResponseData<any>) => {
        if (res.code === 200) {
          setSubmitLoading(false);
          Modal.success({
            title: '',
            onOk: () => {
              initData();
              setHaveModified(false);
              leaveLockRef.current = false;
            }
          });
        } else {
          setSubmitLoading(false);
        }
      })
      .catch(() => {
        setSubmitLoading(false);
      });
  };

  const rangTimeModalOkHandle = () => {
    rangTimeform.validateFields().then((values: KeyValue) => {
      const copyTableData = [...tableData];

      copyTableData[modifyRangTimeIndex]['onRankTime'] = values.onRankTime;

      setTableData(copyTableData);
      message.success('');
      form.resetFields();
      setRangTimeModalVisible(false);
      setHaveModified(true);
      leaveLockRef.current = true;
    });
  };

  const rangTimeModalCancelHandle = () => {
    form.resetFields();
    setRangTimeModalVisible(false);
  };
  
  return (
    <React.Fragment>
      <Prompt when={ haveModified }
        message={ (location: any) => {
          if (!leaveLockRef.current) {
            return true;
          }
          Modal.confirm({
            title: '',
            content: ', ',
            onOk: () => {
              leaveLockRef.current = false;
              setHaveModified(false);
              history.push(location.pathname);
            },
          });

          return !leaveLockRef.current;
        } }
      />
      <div style={{ paddingBottom: '72px' }}>
        <DndProvider backend={ HTML5Backend }>
          <Table columns={ columns }
            loading={ loading }
            dataSource={ tableData }
            components={ components }
            rowKey="dramaId"
            pagination={ false }
            bordered={ true }
            onRow={ (record: any, index: number) => ({
              index,
              moveRow,
            }) }
          />
        </DndProvider>
      </div>
      
      <Row justify="center"
        style={{
          backgroundColor: '#fff',
          position: 'fixed',
          bottom: '0',
          right: '24px',
          width: 'calc(100% - 256px)',
          padding: '20px 0',
          borderTop: '1px solid #ccc'
        }}
      >
        <Col>
          <Popconfirm title=""
            onConfirm={ () => {
              setTableData(baseTableData);
              setHaveModified(false);
              leaveLockRef.current = false;
            } }
          >
            <Button></Button>
          </Popconfirm>
        </Col>
        <Col offset={ 2 }>
          <Button type="primary" onClick={ modifyConfirmhandle } loading={ submitLoading }></Button>
        </Col>
      </Row>
      <Modal title=""
        visible={ modalVisible }
        onOk={ modalOkHandle }
        onCancel={ modalCancelHandle }
      >
        <Form form={ form } labelCol={{ span: 4 }}>
          <Form.Item label="" name="dramaInfo" rules={ [{ required: true }] }>
            <DramaContentSearch />
          </Form.Item>
          <Form.Item label="" name="onRankTime">
            <ExpTime showTimeFormat="HH:mm:ss" />
          </Form.Item>
        </Form>
      </Modal>

      <Modal title=""
        visible={ rangTimeModalVisible }
        onOk={ rangTimeModalOkHandle }
        onCancel={ rangTimeModalCancelHandle }
      >
        <Form form={ rangTimeform } labelCol={{ span: 4 }}>
          <Form.Item label="" name="onRankTime" rules={ [{ required: true, message: '' }] }>
            <ExpTime showTimeFormat="HH:mm:ss" />
          </Form.Item>
        </Form>
      </Modal>
    </React.Fragment>
  );
};

export default withAuthorized(HotRecomment, [Role.SYSTEM]);
// export default HotRecomment;
